<style lang="scss" scoped>
$purple-red: #530030;
$red: #7E0030;
$orange-red: #CA283D;
$orange: #F0443A;
$orange-yellow: #FF7E4A;

// change colour of input placeholder text
*::-webkit-input-placeholder {
    color: #ccc;
}
*:-moz-placeholder {
    /* FF 4-18 */
    color: #ccc;
}
*::-moz-placeholder {
    /* FF 19+ */
    color: #ccc;
}
*:-ms-input-placeholder {
    /* IE 10+ */
    color: #ccc;
}

#tracked-link {
    position: absolute;
    top: -50px;
    z-index: 3;
    display: flex;
    align-items: center;
    height: 50px;
    right: 270px;
    text-decoration: none;

    p {
        font-weight: bold;
        color: $orange-yellow;
        font-size: 16px;

        transition: 0.2s all;

        &:hover {
            color: #F0443A;
        }
    }
}

#search-body {
    position: absolute;
    top: 50px;
    width: 100%;
    min-height: calc(100% - 100px);
    height: calc(100% - 100px);
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-direction: column;

    background-color: #F54;

    #website-description {
        width: 100%;
        height: 50%;
        color: white;
        font-weight: bold;
        display: flex;
        align-items: center;
        justify-content: center;

        h2 {
            width: 600px;
        }
    }

    #not-description {
        width: 100%;
        height: 50%;
        display: flex;
        justify-content: space-around;
    }

    img {
        width: 400px;
        height: 300px;
        margin-top: -100px;
    }


}
</style>

<style lang="scss">
$purple-red: #530030;
$red: #7E0030;
$orange-red: #CA283D;
$orange: #F0443A;
$orange-yellow: #FF7E4A;

#search-body {
#autocomplete-container {
    background-color: white;
    border: 1px solid #ccc;
    box-shadow: 0 0 9px 0 rgba(0,0,0,.3);
    border-radius: 5px;
    position: relative;
    padding: 20px;

    color: #333;
    width: 440px;
    height: 110px;

    #title {
        font-weight: bold;
        font-size: 23px;
    }

    #errorMessage {
        width: 440px;
        color: $orange;
        font-size: 20px;
        font-weight: bold;
        background: white;
        border: 1px solid $orange;
        padding: 10px 5px;
        box-sizing: border-box;
        text-align: center;
        position: absolute;
        top: 115px;
        box-shadow: 0 0 9px 0 rgba(0,0,0,.3);
        cursor: pointer;
        border-radius: 3px;
    }

    #filler {
        display: none;
    }

    form {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        position: relative;
    }

    #input-field {
        box-sizing: border-box;
        padding: 20px;
        font-size: 18px;
        border: 1px solid $orange-yellow;
        outline: none;
        margin: 10px 0 10px 0;
        box-shadow: 0 2px 2px 0 rgba(0,0,0,0.16), 0 0 0 1px rgba(0,0,0,0.08);
        border-radius: 3px;
        background: white;

        transition: box-shadow 200ms cubic-bezier(0.4, 0.0, 0.2, 1);

        &:focus, &:hover {
            //box-shadow: 0 1px 9px 0 rgba(0,0,0,.3);
            box-shadow: 0 3px 8px 0 rgba(0,0,0,0.2), 0 0 0 1px rgba(0,0,0,0.08);
        }
    }

    #submitButton {
        position: absolute;
        right: 8px;
        //margin-top: 12px;
        top: 45px;
        padding: 10px;
        background-color: $orange-yellow;
        color: white;
        font-size: 20px;
        font-weight: bold;
        box-shadow: 0 1px 9px 0 rgba(0,0,0,.3);
        border-radius: 3px;

        cursor: pointer;
        transition: all 0.2s;

        &:hover {
            background-color: $orange;
            box-shadow: 0 5px 9px 0 rgba(0,0,0,.3);
        }
    }

    #search-results {
        border: 1px solid $orange-yellow;
        position: absolute;
        top: 112px;
        list-style: none;
        background-color: white;
        box-sizing: border-box;
        width: inherit;

        li {
            padding: 4px 20px;

            &:hover {
                cursor: pointer;
            }
        }

        .selected {
            background-color: $orange-yellow;
        }
    }
}
}
</style>

<template>
    <div id="search-body">
        <div id="website-description">
            <h2>The easiest way to find upcoming concerts and to keep an eye on your favourite artists</h2>
        </div>

        <div id="not-description">
            <autocomplete
                title="Search for events from an artist:"
                placeholder="Artist name"
                data="static/AllList.json"
                callback="artistSearch"
                submitText="Search">
            </autocomplete>

            <img src="static/map.svg" alt="">
        </div>

        <router-link :to="'/tracked'" id="tracked-link">
            <p>Tracked Artists</p>
        </router-link>
    </div>
</template>

<script>
import store from '@/store/index.js'

export default {
    data: function() {
        // Declare local data variables
        return {
        }
    },

    watch: {
    },

    mounted: function() {
        document.getElementById('input-field').focus();
    },

    methods: {
        callBackForm: function(callback, value) {
            if (callback == "artistSearch") {
                let artist = value;

                // Check if we get a response from BIT API before we redirect
                store.doesArtistExist(artist).then(data => {
                    // If the response contains an ID redirect to artist-view
                    if (data.id) {
                        this.$router.push({ path: "/" + "artists/" + artist })
                    }
                }).catch(error => {
                    // If we get an error that means the artist has not been found
                    if (error.toString().includes("SyntaxError")) {
                        this.$children[0].errorMessage = "Sorry, we couldn't find that artist :(";
                    }
                })
            }
        },
    }
}
</script>



